.container {
  max-width: 1160px;
  margin: 0 auto;
  padding: 0 10px; }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px; } }

@media (max-width: 719px) {
  .container {
    max-width: 719px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  width: 100%;
  height: auto; }

.hidden {
  display: none; }

body {
  font-family: "Rajdhani", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  flex-grow: 1; }

.include {
  display: none; }

.main {
  display: grid;
  grid-template-columns: 1fr;
  flex-grow: 1; }

