.contact {
  background-color: #014550;
  margin-bottom: 50px;
  display: grid;
  grid-template-columns: 57% 43%; }

@media (min-width: 2560px) {
  .contact {
    grid-template-columns: 56% 44%; } }

@media (min-width: 3200px) {
  .contact {
    grid-template-columns: 55% 45%; } }

@media (min-width: 4600px) {
  .contact {
    grid-template-columns: 53% 47%; } }

.contact__wrapper {
  display: flex; }

@media (max-width: 1023px) {
  .contact {
    grid-template-columns: 1fr;
    margin-bottom: 30px; } }

.contact__information {
  background-color: #014550;
  background-image: url("../img/top-img.png");
  background-repeat: no-repeat;
  background-position: 99% 76%;
  background-size: 26vh;
  position: relative; }

.contact__information:after {
  position: absolute;
  content: "";
  background-image: url("../img/mixed-dots.png");
  width: 100px;
  height: 503px;
  bottom: -181px;
  left: 0;
  background-repeat: no-repeat; }

.information__inner {
  max-width: 820px;
  padding-top: 50px;
  padding-bottom: 80px;
  margin-right: 0;
  padding-left: 115px; }

@media (min-width: 2560px) {
  .information__inner {
    max-width: 870px; }
  .contact__information {
    background-size: 20vh; } }

.information__image {
  height: 52px;
  width: 149px; }

.information__data {
  padding-top: 60px; }

.information__title,
.information__text,
.information__header {
  margin-top: 0; }

.information__title {
  margin-bottom: 15px;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  color: #83d825; }

.information__header,
.information__text {
  color: #ffffff; }

.information__header {
  margin-bottom: 30px;
  font-size: 54px;
  line-height: 54px;
  max-width: 450px;
  word-spacing: 2px; }

.information__text {
  margin-bottom: 0; }

@media (max-width: 1130px) {
  .contact__information:after {
    left: -26px; } }

@media (max-width: 1023px) {
  .information__inner,
  .information__header {
    max-width: 100%; }
  .information__inner {
    padding-left: 10px; }
  .contact__information {
    background-position: 88% 94%;
    background-size: 17vh; }
  .contact__information:after {
    display: none; } }

@media (min-resolution: 2dppx) {
  .contact__information {
    background-image: url("../img/top-img@2x.png"); } }

.form {
  position: relative; }

.form::after {
  position: absolute;
  content: "";
  background-image: url("../img/dots.png");
  width: 68px;
  height: 503px;
  top: -55px;
  right: 0;
  background-repeat: no-repeat;
  z-index: 0; }

.contact__form {
  background-color: #013741; }

.form__inner {
  max-width: 1120px;
  padding-top: 50px;
  padding-bottom: 90px;
  max-width: 800px;
  margin-left: 0;
  padding-left: 50px;
  position: relative;
  z-index: 1; }

@media (max-width: 1060px) {
  .form__inner {
    padding-left: 30px; } }

.form__body {
  width: 100%; }

.form__title {
  margin-top: 0;
  margin-bottom: 20px;
  color: #ffffff; }

.form__inputs {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px; }

.form__label:not(:last-child) {
  margin-bottom: 10px; }

.form__input {
  padding-left: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: #ffffff;
  color: #000000;
  border: 0;
  min-width: 390px;
  font-size: 16px;
  line-height: 40px; }

.form__checkboxes {
  margin-bottom: 10px; }

.checkbox__label {
  position: relative;
  display: flex;
  min-height: 20px;
  align-items: center;
  cursor: pointer; }

.checkbox__input {
  position: absolute;
  appearance: none; }

.checkbox__text,
.form__text {
  color: #ffffff; }

.checkbox__text {
  background-image: url("../img/checkbox.svg");
  background-repeat: no-repeat;
  padding-left: 35px;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  max-width: 330px; }

.checkbox__input:checked + .checkbox__text {
  background-image: url("../img/checkbox-checked.svg");
  background-repeat: no-repeat; }

.form__policy {
  margin-bottom: 25px; }

.form__text {
  margin: 0; }

.form__link {
  color: #76c626; }

.form__link:hover,
.form__link:focus {
  opacity: 0.5; }

.form__button {
  width: 100%;
  border: none;
  color: #127046;
  background-color: #93ea21;
  max-width: 390px;
  border-radius: 20px;
  padding: 12px 20px;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer; }

.form__button:hover,
.form__button:focus {
  background-color: #ffffff;
  color: #000000; }

@media (max-width: 1023px) {
  .form__inner {
    max-width: 100%;
    padding-bottom: 50px;
    padding-left: 10px; }
  .form__input {
    min-width: 100%; }
  .checkbox__text {
    max-width: 100%; }
  .form__button {
    max-width: 100%; }
  .form:after {
    display: none; } }

.partnership {
  margin-bottom: 50px; }

.partnership__inner {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  position: relative; }

.partnership__inner::after {
  content: "";
  border-bottom: 2px solid #f1f1f1;
  position: absolute;
  bottom: -20px;
  width: 98%; }

.partnership__header {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 40px;
  line-height: 48px;
  text-align: center; }

.partnership__programm {
  display: flex;
  justify-content: space-between; }

.partnership__information {
  padding-left: 10px;
  max-width: 660px; }

.video__body {
  border-radius: 20px;
  width: 365px;
  height: 209px;
  cursor: pointer; }

.video__body iframe {
  border: 0; }

.video__text {
  margin-bottom: 0;
  margin-top: 10px;
  color: #b7b7b7;
  font-weight: 700; }

.partnership__text,
.partnership__benefit,
.partnership__list {
  margin-top: 0; }

.partnership__text {
  margin-bottom: 20px; }

.partnership__benefit {
  margin-bottom: 0; }

.partnership__list {
  margin-bottom: 25px;
  padding-left: 17px; }

.partnership__item {
  margin-bottom: 10px; }

.partnership__details {
  margin-bottom: 15px; }

.partnership__access {
  display: flex;
  justify-content: space-between; }

.access__header {
  margin: 0;
  max-width: 270px; }

.access__button {
  padding: 0 50px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #00b236;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  max-height: 45px; }

.access__button:hover,
.access__button:focus {
  background-color: #ffffff;
  border: 1px solid #013741;
  color: #000000; }

@media (max-width: 1023px) {
  .partnership__header {
    margin-bottom: 20px;
    font-size: 32px;
    line-height: 38px; }
  .partnership__programm {
    flex-direction: column; }
  .partnership__video {
    margin-bottom: 20px; }
  .video__body {
    width: 100%;
    height: 100%; }
  .partnership__information {
    max-width: 100%;
    padding-left: 0; }
  .access__header {
    max-width: 100%; }
  .access__button {
    padding: 10px 50px; } }

@media (max-width: 719px) {
  .partnership__access {
    flex-direction: column; }
  .partnership__inner::after {
    width: 95%; }
  .access__header {
    margin-bottom: 10px;
    text-align: center; }
  .access__button {
    padding-top: 10px;
    padding-bottom: 10px; } }

.experts {
  margin-bottom: 50px; }

.experts__inner {
  display: flex;
  flex-direction: column; }

.experts__header {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 40px;
  line-height: 48px; }

.experts__list {
  margin-top: 0;
  list-style: none;
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(210px, 1fr));
  grid-column-gap: 20px;
  grid-row-gap: 30px; }

.experts__item {
  position: relative;
  max-width: 210px;
  cursor: pointer; }

.experts__item::before {
  content: "";
  position: absolute;
  background-image: url("../img/dots-white.png");
  background-repeat: no-repeat;
  width: 130px;
  height: 164px;
  top: -20px;
  right: 3px;
  background-color: transparent; }

.experts__item--active .experts__image {
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4); }

.experts__image {
  position: relative;
  border-radius: 50px;
  max-width: 164px;
  max-height: 158px; }

.experts__title {
  margin-top: 10px;
  margin-bottom: 5px;
  font-size: 32px;
  line-height: 40px; }

.experts__text {
  margin-bottom: 0;
  margin-top: 0;
  color: #b7b7b7; }

.experts__text span {
  text-transform: uppercase; }

@media (max-width: 1023px) {
  .experts {
    margin-bottom: 30px; }
  .experts__header {
    font-size: 32px;
    line-height: 38px;
    text-align: center; }
  .experts__list {
    margin-left: 10%;
    margin-right: 10%; }
  .experts__item::before {
    top: -21px;
    right: 3px; } }

@media (max-width: 719px) {
  .experts__list {
    grid-column-gap: 30px;
    justify-items: center; }
  .experts__item {
    position: relative;
    max-width: 140px; }
  .experts__item::before {
    top: -23px;
    left: 52px;
    right: 0; }
  .experts__title {
    font-size: 22px;
    line-height: 28px;
    margin-top: 25px; }
  .experts__text {
    font-size: 12px;
    line-height: 14px; }
  .experts__details {
    text-align: center; } }

.footer {
